body {
    margin: 0;
    font-family: sans-serif;
    background: #f3f4f6;
  }

  section {
    height: 100vh;
    display: flex;
  }

  .box {
    width: 120px;
    height: 120px;
    background: #4f46e5;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    border-radius: 12px;
  }

  .timeline {
    display: flex;
    gap: 20px;
  }

  .item {
    width: 80px;
    height: 80px;
    background: #22c55e;
    border-radius: 10px;
  }

  .pin-section {
    background: #111827;
  }

  .pin-text {
    color: white;
    font-size: 48px;
  }